import React from 'react'
import { BrowserRouter as Router, Route, Redirect, Link } from 'react-router-dom';

import Container from 'react-bootstrap/Container';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import './App.css';

import Footer from './components/Footer'
import HomePage from './pages/HomePage'
import AboutPage from './pages/AboutPage'
import ContactPage from './pages/ContactPage'
import Loader from './components/Loader.js'


class App extends React.Component {


  constructor(props){
    super(props);
    this.state = {
      title: 'Mark Angus',
      loading: true,
      navLinks: [
        { title: 'Home', path: '/home'},
        { title: 'About', path: '/about'},
        { title: 'Contact', path: '/contact'},
      ],
      home: {
        title: 'Computer Science Graduate',
        subTitle: 'Graduated July 2021',
        para: 'Checkout my projects below',
      },
      about: {
        title: 'About me',
      },
      contact: {
        title: 'Don\'t hesitate to contact me!',
      },
    }
  }
 
  componentDidMount() {
    this.timerHandle = setTimeout(() => this.setState({ loading: false }), 1000); 
  }
  componentWillUnmount(){
    if (this.timerHandle) {
      clearTimeout(this.timerHandle);
      this.timerHandle = 0;
    }
  }
  render(){
    return (
      <Router  basename='/react-project/portfolio'>
        <Container className="p-0" fluid={true}>

          <Navbar className="text-white border-bottom" bg="dark" variant="dark" expand="lg">
            <Navbar.Brand className="ms-3"><Link className="nav-link title-name" to="/react-project/portfolio/home">{this.state.title}</Link></Navbar.Brand>
            <Navbar.Toggle className="border-0" aria-controls="navbar-toggle" />
            <Navbar.Collapse id="navbar-toggle">
              <Nav className="ms-auto me-3 text-center">
                <Link className="nav-link" to="/react-project/portfolio/home">Home</Link>
                <Link className="nav-link" to="/react-project/portfolio/about">About</Link>
                <Link className="nav-link" to="/react-project/portfolio/contact">Contact</Link>              
              </Nav>
            </Navbar.Collapse>
          </Navbar>

          <Container className="body-height">

          {this.state.loading === true && <Loader />}
          
          <Route exact path="">
            <Redirect to="/react-project/portfolio/home"/>
          </Route>
            <Route path="/react-project/portfolio/home" render={() => 
            <HomePage classname=""
                      title={this.state.home.title}
                      subTitle={this.state.home.subTitle}
                      para={this.state.home.para} 
                      
            />} />
            <Route path="/react-project/portfolio/about" render={() => 
            <AboutPage 
                      title={this.state.about.title}
            />} />

            <Route path="/react-project/portfolio/contact" render={() => 
            <ContactPage 
                      title={this.state.contact.title}
            />} />
            
          </Container>
        </Container>
        <Footer />
      </Router>
    );

  }
  
}

export default App;
